<script type="text/ecmascript-6">
export default {
  name: 'UserInfo',
  data () {
    return {
      collapse: 1,
      restorationlistColumns: [
        {
          title: '',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: '',
          key: 'restorationName'
        },
        {
          title: '',
          key: 'restorationImg'
        },
        {
          title: '',
          key: 'restorationImg'
        },
        {
          title: '',
          key: 'restorationDesc'
        }
      ],
      restorationlistData: [],
      restorationpager: {
        total: 1,
        current: 1
      }
    }
  },
  created () {
    // this.getRestorationList(1)
  },
  methods: {
    getRestorationList (pageNo) {
      this.http.get('/api/restoration/page/' + (pageNo || 1)).then(res => {
        if (res.error === false) {
          this.restorationpager = res.result
          this.restorationlistData = res.result.records
        }
      })
    },
    changePage () {
      this.getRestorationList(this.restorationpager.current)
    }
  }
}
</script>
<style scoped lang='stylus' rel="stylesheet/stylus">
.UserInfo
  .userInfo
    width:100%
    height:100px
    padding-top:10px
    .userAvater
      width:100%
      text-align:center
      .avaters
        width:70px
        height:auto
        max-height:70px
        border-radius:50%
        margin:0px auto
    .userDetail
      ul
        li
          width:100%
          height:20px
          line-height:20px
          text-align:left
  .tabWrapper
    min-height:250px
    .attendActivity
      li
        width:100%
        list-style:none
        height:50px
        border-bottom:1px dashed #ccc
        text-align:center
        display:flex
        margin-bottom:5px
        .activityImg
          width:50px
          float:left
          img
            height:45px
            width:auto
            max-width:45px
            margin:0px auto
        .activityName
          flex:2
          text-align:left
          overflow-y:hidden
        .activityInfo
          flex:1
        .income
          flex:1
          line-height:50px
    .inviterList
      .inviteravater
        text-align:center
        .avaterWrapper
          padding:5px 5px 0px
          img
            width:calc(100% - 10px)
            height:auto
            max-height:50px
            border-radius:5px
        .invitername
          width:100%
          font-size:0.8em
          overflow-y:hidden
          height:18px
          line-height:18px
</style>



