<template>
  <div class = "gift">
    <mainHead :val="value"></mainHead>
    <div class="section_gift">
      <img src="/static/images/up.png" class = "upImg">
      <div class="gifttitle">
        {{activity.shareTimes}}
      </div>
        
      <div class = "gift_img">
        <img :src='murl + activity.activityImg' class = "main_img">
      </div>
      <img src="/static/images/down.png" class="foot">
    </div>
  </div>
</template>

<script>
  import mainHead from '../Utils/mainHead.vue'
  export default {
    name: 'joinPeople',
    props: ['activity'],
    components: {mainHead},
    watch: {
      activity: function (val, oldVal) {
        this.value.name = val.companyName
      }
    },
    data () {
      return {
        value: {
          head: '',
          name: null,
          no: 4
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  rrem(val){
    return (val/144px)rem
  }
  .gift
    width:100%
    height auto
    position relative
    background #fff
    .section_gift
      width:96%
      margin auto
      position relative
      .gifttitle
        width:100%
        height auto
        padding:10px 0px
        display:inline-block
        text-align:center
        font-weight:bolder
      .upImg
        position absolute
        top rrem(-75px)
        left rrem(20px)
        width 100%
      .foot
        width 100%
        margin-top rrem(-320px)
        margin-left rrem(-30px)
      .gift_img
        width 97%
        margin auto
        img
          width 100%
          display block
          margin auto
</style>



